.blockedUser{
    width: 100%;
    height: fit-content;
    padding: 16px;
    background: white;
    border-radius: 12px;
    box-shadow: 0px 4px 10px rgb(166, 166, 166);
    display: flex;
    flex-direction: column;
    justify-content: start;
    gap: 12px;

    .blockHeader{
        height: fit-content;
        width: 100%;
        display: flex;
        align-items: center;
        padding: 0;
        margin: 0;

        & img{
            width: 36px;
            height: 36px;
            object-fit: cover;
            border-radius: 50%;
            margin-right: 8px;
        }

        & h2{
            cursor: pointer;
            margin-right: auto;
            font-size: 1.5rem;

            &:hover{
                color: rgb(108, 108, 108);
            }

            &:active{
                color: rgb(63, 63, 63);
            }
        }

        .menu{
            position: relative;

            & svg{
                cursor: pointer;
            }

            & ul{
                position: absolute;
                background: rgb(81, 81, 81);
                top: 100%;
                left: -100%;
                width: fit-content;
                color: white;
                align-items: center;
                padding: 0;
                gap: 0;
                border-radius: 4px;
                overflow: hidden;
                transform-origin: 0 0;
                z-index: 2;

                & li{
                    width: 100%;
                    text-align: center;
                    cursor: pointer;
                    padding: 8px 12px;
                }

                & li:hover{
                    background: rgb(61, 61, 61);
                }

                & li:active{
                    background: rgb(51, 51, 51);
                }
            }
        }
    }

    .blockedUser{
        width: 100%;
        gap: 0px;

        .blockHeader{
            margin-bottom: 16px;

            & img{
                width: 32px;
                height: 32px;
            }

            & h2{
                font-size: 1.3rem;
            }
        }

        & label{
            font-size: 1.1rem;
            font-weight: 600;
            margin-bottom: 4px;
            margin-left: 4px;
        }

        & p{
            width: 100%;
            padding: 12px;
            background: #e9e9e9;
            border-radius: 8px;
        }
    }
}