.BlocksSectionContainer{
    width: 100%;
    height: 100vh;

    .returnDiv{
        width: 54px;
        height: 54px;
        cursor: pointer;
        position: absolute;
        right: 0;
        bottom: 0;
        margin-bottom: 16px;
        margin-right: 16px;
        background: white;
        box-shadow: 1px 2px 16px #d0d0d0;
        border-radius: 50%;
        z-index: 5;
        transition: all 0.1s ease-in;

        & svg{
            width: 100%;
            height: 100%;
            padding: 12px;
        }
    }

    .returnDiv:hover{
        box-shadow: 1px 2px 16px #9f9f9f;
        transition: all 0.1s ease-in;
    }

    .BlocksSection{
        width: 100%;
        height: 100%;
        overflow-y: auto;

        & ul{
            width: 100%;
            max-width: 700px;
            height: fit-content;
            display: flex;
            flex-direction: column;
            padding: 8px;

        }

        &::-webkit-scrollbar{
            display: none;
        }
    }
}